/* Contenitore generale della pagina profilo */
.profile-page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  padding: 20px;
  background: linear-gradient(135deg, #eef2f6, #ffffff);
  color: #333;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

/* Card profilo */
.profile-card-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background: #ffffff;
  border: 1px solid #ddd;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  animation: fadeIn 0.5s ease-in-out;
}

/* Intestazione della card */
.profile-card-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  text-align: center;
  margin-bottom: 15px;
  border-bottom: 1px solid #ddd;
  padding-bottom: 10px;
}

.profile-card-header h3 {
  font-size: 1.6rem;
  font-weight: bold;
  color: #222;
  word-break: break-word; /* Per prevenire overflow */
}

/* Pulsante modifica profilo */
.profile-edit-button {
  padding: 10px 15px;
  background: linear-gradient(135deg, #0073e6, #005bb5);
  color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  font-size: 1rem;
  margin-top: 10px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.profile-edit-button:hover {
  background: linear-gradient(135deg, #005bb5, #003f8a);
  box-shadow: 0 6px 12px rgba(0, 115, 230, 0.3);
  transform: translateY(-2px);
}

/* Sezione informativa */
.profile-card-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  width: 100%;
  text-align: center;
  line-height: 1.5;
}

.profile-card-info p {
  font-size: 1rem;
  color: #555;
  word-wrap: break-word; /* Per permettere il ritorno a capo */
}

/* Azioni account */
.profile-account-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  width: 100%;
}

.profile-action-button,
.profile-logout-button {
  flex: 1;
  max-width: 180px;
  padding: 10px;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  color: #fff;
  cursor: pointer;
  text-align: center;
  transition: all 0.3s ease;
}

.profile-action-button {
  background: linear-gradient(135deg, #28a745, #218838);
}

.profile-action-button:hover {
  background: linear-gradient(135deg, #218838, #1e7e34);
  box-shadow: 0 4px 8px rgba(33, 150, 243, 0.3);
}

.profile-logout-button {
  background: linear-gradient(135deg, #dc3545, #c82333);
}

.profile-logout-button:hover {
  background: linear-gradient(135deg, #c82333, #a71d2a);
  box-shadow: 0 4px 8px rgba(220, 53, 69, 0.3);
}

/* Responsive: max-width 768px */
@media (max-width: 768px) {
   .profile-card-container {
    width: 100%;
    max-width: 100%;
    padding: 15px;
    margin: 0 10px; /* Margine sugli assi X */
  }

  .profile-card-header {
    flex-direction: column;
    gap: 5px;
    padding: 0 10px;
  }

  .profile-card-header h3 {
    font-size: 1.4rem;
    word-wrap: break-word;
  }
.profile-edit-button {
    padding: 8px 12px;
    font-size: 0.9rem;
    margin-bottom: 15px;
  }
  .profile-card-info p {
    font-size: 1rem;
    word-wrap: break-word;
  }

  .profile-account-actions {
    flex-direction: column;
    gap: 10px;
  }

  .profile-action-button,
  .profile-logout-button {
    max-width: 100%;
  }
}

/* Responsive: max-width 450px */
@media (max-width: 450px) {
  .profile-card-container {
    padding: 15px;
    margin: 0 15px; /* Margine sugli assi X */
  }

  .profile-card-container {
    padding: 15px;
    margin: 15px; /* Aggiunto margine per distanziamento */
  }

  .profile-card-header h3 {
    font-size: 1.2rem;
    line-height: 1.4;
    word-break: break-word;
    max-width: 100%;
    text-align: center;
  }

  .profile-card-info p {
    font-size: 0.9rem;
    text-align: center;
    line-height: 1.4;
  }

  .profile-edit-button {
    font-size: 0.9rem;
    padding: 8px 12px;
    margin-bottom: 15px;
  }

  .profile-action-button,
  .profile-logout-button {
    font-size: 0.9rem;
    padding: 8px 10px;
    max-width: 100%;
  }

  .profile-footer {
    font-size: 0.75rem;
  }
}
